
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(30px, 0);
  transform: translate(500px, 0);
  -webkit-transition: 0.6s;
  transition: 0.6s;
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-30px, 0);
  transform: translate(-500px, 0);
  -webkit-transition: 0.6s;
  transition: 0.6s;
}



