
.top-stories {
  position: relative;
}
.top-stories .container {
  max-width: 640px;
  margin: 0 auto;
  max-height: 375px;
}
.top-stories .imgs {
  position: relative;
  width: 100%;
  padding-bottom: 65%;
  box-sizing: border-box;
  overflow: hidden;
}
.top-stories .imgs .img.is-active {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  z-index: 0;
}
.top-stories .imgs .img.is-leaving {
  -webkit-transform: translate(-640px, 0);
          transform: translate(-640px, 0);
}
.top-stories .imgs .img {
  position: absolute;
  width: 100%;
  max-width: 640px;
  max-height: 375px;
  -webkit-transform: translate(640px, 0);
          transform: translate(640px, 0);
  overflow: hidden;
  z-index: -1;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
.top-stories .imgs .img img {
  width: 100%;
  height: 100%;
  margin: -18% 0;
}
.top-stories .imgs .img .title {
  position: absolute;
  bottom: 8%;
  width: 100%;
  font-size: 24px;
  text-align: center;
  color: #fff;
  margin: 0 !important;
}
.top-stories nav {
  position: absolute;
  bottom: 1%;
  width: 100%;
  max-width: 640px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.top-stories .slide-left-enter,
.top-stories .slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(500px, 0);
  transform: translate(500px, 0);
  -webkit-transition: 0.6s;
  transition: 0.6s;
}
.top-stories .slide-left-leave-active,
.top-stories .slide-right-enter {
  opacity: 0;
  -webkit-transform: translate(-500px, 0);
  transform: translate(-500px, 0);
  -webkit-transition: 0.6s;
  transition: 0.6s;
}


